/* black overlay */

[data-overlay] {
	position: relative;
}
[data-overlay]::before {
	background: #000 none repeat scroll 0 0;
	content: "";
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 1;
}
[data-overlay="3"]::before {
	opacity: 0.3;
}
[data-overlay="4"]::before {
	opacity: 0.4;
}
[data-overlay="5"]::before {
	opacity: 0.5;
}
[data-overlay="6"]::before {
	opacity: 0.6;
}
[data-overlay="7"]::before {
	opacity: 0.7;
}
[data-overlay="8"]::before {
	opacity: 0.8;
}
[data-overlay="9"]::before {
	opacity: 0.9;
}

/* Floating Labels */

[dir=rtl] .floating-label > input + label,
[dir=rtl] .floating-label > textarea + label {
  right: 0;
  left: auto !important;
}
[dir=rtl] .floating-label > input:not([placeholder]):not([value]) + label,
[dir=rtl] .floating-label > textarea:not([placeholder]):empty + label, [dir=rtl] .floating-label > input[placeholder]:not(:placeholder-shown) + label,
[dir=rtl] .floating-label > textarea[placeholder]:not(:placeholder-shown) + label, [dir=rtl] .floating-label > input:-webkit-autofill + label,
[dir=rtl] .floating-label > textarea:-webkit-autofill + label, [dir=rtl] .floating-label > input:focus + label,
[dir=rtl] .floating-label > textarea:focus + label, [dir=rtl] .floating-label > select + label {
  right: 0.5rem;
  left: auto !important;
}

.floating-label {
  position: relative;
}
.floating-label > select + label,
.floating-label > select + label.label-sm {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  left: 0.5rem;
  padding: 0 0.3rem;
  color: inherit;
  background-color: #fff;
  border-radius: 0.25rem;
  top: -0.7875rem;
  font-size: 0.875rem;
}
.floating-label > select + label.label-md {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  left: 0.5rem;
  padding: 0 0.3rem;
  color: inherit;
  background-color: #fff;
  border-radius: 0.25rem;
  top: -0.9rem;
  font-size: 1rem;
}
.floating-label > select + label.label-lg {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  left: 0.5rem;
  padding: 0 0.3rem;
  color: inherit;
  background-color: #fff;
  border-radius: 0.25rem;
  top: -1.125rem;
  font-size: 1.25rem;
}
.floating-label > select:disabled + label, .floating-label > select[readonly] + label {
  background-color: #e9ecef;
}
.floating-label > input + label,
.floating-label > textarea + label {
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  top: 0;
  left: 0;
  color: #6c757d;
  pointer-events: none;
  font-size: 1rem;
  padding: calc(0.375rem + 1px) calc(0.75rem + 1px);
}
.floating-label > input.form-control-sm + label,
.floating-label > textarea.form-control-sm + label {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  top: 0;
  left: 0;
  color: #6c757d;
  pointer-events: none;
  font-size: 0.875rem;
  padding: calc(0.25rem + 1px) calc(0.5rem + 1px);
}
.floating-label > input.form-control-lg + label,
.floating-label > textarea.form-control-lg + label {
  position: absolute;
  z-index: 3;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  top: 0;
  left: 0;
  color: #6c757d;
  pointer-events: none;
  font-size: 1.25rem;
  padding: calc(0.5rem + 1px) calc(1rem + 1px);
}
.floating-label > input::placeholder,
.floating-label > textarea::placeholder {
  color: transparent;
  transition: color 150ms cubic-bezier(0.4, 0, 0.2, 1);
}
.floating-label > label + input::placeholder,
.floating-label > label + textarea::placeholder {
  color: #6c757d;
}
.floating-label > input:focus::placeholder,
.floating-label > textarea:focus::placeholder {
  color: #6c757d;
}
.floating-label > input:focus + label,
.floating-label > input:focus + label.label-sm,
